.mainContainer {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.contentContainer {
  display: flex;
  flex-direction: row;
  overflow: hidden;
  flex: 1;
  min-width: 960px;
}

.sidePanel {
  width: 360px;
  overflow-y: auto;
}

.mainContent {
  flex: 1;
  overflow-y: auto;
  display: flex;
  justify-content: center;
}

