.title {
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  color: #696969;
  padding-left: 60px;
  padding-top: 16px;
  padding-bottom: 15px;
  margin: 0px;
  width: 300px;
  cursor: pointer;
}

.container {
  display: flex;
  flex-direction: column;
}

.container .row {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.searchContainer {
  flex: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.delimiter {
  height: 1px;
  background-color: #E5E5E5;
}